import React from 'react'
import * as Colors from '../../lib/utils/colors';

const styles = {
  tagsContainer: {
    flexDirection: 'row',
    display: 'flex',
    marginTop: 10,
    fontSize: 14,
  },
  tag: {
    marginRight: 10,
    backgroundColor: 'grey',
    padding: 5,
    paddingLeft: 8,
    paddingRight: 8,
    borderRadius: 6,
    fontWeight: 'bold',
  },
};

export default function TagsList({ data }) {
  return (
    <div
      style={styles.tagsContainer}
    >
      {
        (data || []).map((item, index) => {
          const color = Colors.tags[item];
          let style = { ...styles.tag };
          if (color) {
            style.color = color;
            style.backgroundColor = Colors.alpha(color, 0.3);
          }

          return (
            <div
              style={style}
              key={`tag-${index}`}
            >
              {item}
            </div>
          );
        })
      }
    </div>
  );
}



