import Color from 'color';

// blog tags
export const tags = {
  'realm-db': '#f5595e',
  'offline-first': '#a047d9',
  'e-commerce': '#00a0ff',
  'tool': '#40407a',
  payment: '#00b960',
  aws: '#ffa502',
  docker: '#aaa69d',
};

// utils
export const alpha = (inputColor, alphaValue) => {
  if (!alphaValue) return inputColor;
  return Color(inputColor).alpha(alphaValue);
};




